/* VARIABLES */
#Service {
  position: relative; }
  #Service:before, #Service:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 50%;
    left: 0;
    right: 0;
    z-index: -2;
    background-image: url(./bg.png);
    background-attachment: fixed;
    background-position: center;
    background-size: cover; }
  #Service:after {
    top: 50%;
    bottom: 0;
    background: #fafafb; }
  #Service .card {
    border: none;
    margin: 0 5%;
    padding: 7% 0;
    -webkit-box-shadow: 3px 3px 40px 0px #212121;
    box-shadow: 3px 3px 40px 0px #212121; }



