#Portfolio {
  background-color: #fafafb; }
  #Portfolio * {
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out; }
  #Portfolio .project .image-info {
    overflow: hidden;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    background-color: #FFF;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }
    #Portfolio .project .image-info:hover {
      -webkit-box-shadow: 0 3px 20px rgba(0, 0, 0, 0.19), 0 3px 12px rgba(0, 0, 0, 0.23);
      box-shadow: 0 3px 20px rgba(0, 0, 0, 0.19), 0 3px 12px rgba(0, 0, 0, 0.23);
      -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
      transform: scale(1.02); }
      #Portfolio .project .image-info:hover .info {
        -webkit-transform: translate(0, 10%);
        -ms-transform: translate(0, 10%);
        transform: translate(0, 10%); }
    #Portfolio .project .image-info .info {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      -webkit-transform: translate(0, 110%);
      -ms-transform: translate(0, 110%);
      transform: translate(0, 110%); }
      #Portfolio .project .image-info .info:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        -webkit-filter: blur(4px);
        filter: blur(4px);
        background-color: rgba(0, 0, 0, 0.7); }
  #Portfolio .slick-initialized .slick-slide {
    display: inline-block;
    vertical-align: middle;
    float: none; }
  #Portfolio .close {
    position: absolute;
    right: 5%;
    z-index: 999999; }
  #Portfolio a {
    text-decoration: none; }

#Counter {
  position: relative;
  margin-top: 8%;
  padding: 5% 0; }
  #Counter h1 {
    font-size: 6rem; }
  #Counter:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(./bg.png);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.5); }



